<template>
  <div>
    <v-tabs v-model="tab" fixed-tabs dark>
      <v-tab :key="0"> Data </v-tab>
      <v-tab :key="1"> Debug Messages </v-tab>
    </v-tabs>
    <v-tabs-items v-model="tab">
      <v-tab-item :key="0">
        <router-view></router-view>
      </v-tab-item>
      <v-tab-item :key="2">
        <debug-messages :topic="topic"></debug-messages>
      </v-tab-item>
    </v-tabs-items>
  </div>
</template>

<script>
  import axios from "../../axios";
  import DebugMessages from '../Common/DebugMessages.vue';
  export default {
    components: {
      DebugMessages
    },
    data: () => ({
      topic: 'rubix/points/debug',
      tab: 0,
    }),
    beforeRouteLeave (to, from, next) {
      axios.post(`mqtt/unsubscribe`, {topic: this.topic}).then(r => {
        next();
      });
    },
  };
</script>


